<!--  -->
<template>
  <div class="">
    <van-picker
      title="标题"
      show-toolbar
      :columns="columns"
      @confirm="onConfirm"
      @cancel="onCancel"
      @change="onChange"
      :default-index="value"
    ></van-picker>
    <!-- 设置初始选中值 -->
  </div>
</template>

<script>
// 这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
// 例如：import 《组件名称》 from '《组件路径》';

import { updateUserProfile } from '@/api/user.js'

export default {
// import引入的组件需要注入到对象中才能使用
  name: 'UpdateGender',
  props: { value: Number }, // 接收数据
  components: {},
  data  () {
    // 这里存放数据
    return {
      columns: ['男', '女'],
      currentValue: this.value // 定义当前值
    }
  },
  // 监听属性 类似于data概念
  computed: {},
  // 监控data中的数据变化
  watch: {},
  // 方法集合
  methods: {
    async onConfirm (value, index) {
      // 确定操作
    // 发送请求
      await updateUserProfile({ gender: this.currentValue })
      // 提示成功
      this.$toast.success('修改成功')
      // 修改数据
      this.$emit('input', this.currentValue)
      // 关闭弹层
      this.$emit('close')
    },
    onChange  (picker, value, index) {
      this.currentValue = index // 切换改变当前值
    },
    onCancel () {
      this.$emit('close') // 取消操作
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created  () {

  },
  // 生命周期 - 挂载完成（可以访问DOM元素）
  mounted  () {

  },
  beforeCreate  () {}, // 生命周期 - 创建之前
  beforeMount  () {}, // 生命周期 - 挂载之前
  beforeUpdate  () {}, // 生命周期 - 更新之前
  updated  () {}, // 生命周期 - 更新之后
  beforeDestroy  () {}, // 生命周期 - 销毁之前
  destroyed  () {}, // 生命周期 - 销毁完成
  activated  () {} // 如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang='less' scoped>
// @import url(); 引入公共css类
</style>
