<!--  -->
<template>
  <div class="">
    <van-nav-bar title="个人信息" class="page-nav-bar">
      <van-icon slot="left" name="cross" @click="$router.back()"></van-icon>
    </van-nav-bar>
    <!-- 触发文件的选择 -->
    <van-cell title="头像" is-link @click="$refs.fileRef.click()">
      <van-image round fit="cover" class="avatar" :src="user.photo"></van-image>
    </van-cell>
    <!-- 创建文件选择标签 -->
    <input ref="fileRef" type="file" hidden @change="onChangeFile"/>
    <!-- 测试读取图片是否成功 -->
    <!-- <img :src="imgPath"/> -->
    <van-cell
      title="昵称"
      is-link
      :value="user.name"
      @click="isUpdateNameShow = true"
    ></van-cell>
    <van-cell
      title="性别"
      @click="isUpdateGenderShow = true"
      is-link
      :value="user.gender === 1 ? '女' : '男'"
    ></van-cell>
    <van-cell title="生日" is-link :value="user.birthday" @click="isUpdateBirthdayShow=true"></van-cell>
    <van-popup
      v-model="isUpdateNameShow"
      position="bottom"
      :style="{ height: '100%' }"
    >
      <!-- 重置数据方式2 通过 v-if 销毁组件 -->
      <update-name
        v-if="isUpdateNameShow"
        v-model="user.name"
        @close="isUpdateNameShow = false"
      ></update-name>
    </van-popup>
    <!-- 定义弹层,使用组件 -->
    <van-popup v-model="isUpdateGenderShow" position="bottom">
    <!-- 解决重置数据和传数据 -->
      <update-gender
        v-if="isUpdateGenderShow"
        v-model="user.gender"
        @close="isUpdateGenderShow = false"
      ></update-gender>
    </van-popup>
     <van-popup v-model="isUpdateBirthdayShow" position="bottom">
      <update-birthday
        v-if="isUpdateBirthdayShow"
        v-model="user.birthday"
        @close="isUpdateBirthdayShow = false"
      ></update-birthday>
    </van-popup>
    <!-- 新建弹层传文件内容 -->
    <van-popup v-model="isUpdatePhotoShow" position="bottom" :style="{height:'100%'}">
      <update-photo
        v-if="isUpdatePhotoShow"
        :img-path="imgPath"
        @input="user.photo=$event"
        @close="isUpdatePhotoShow = false"
      ></update-photo>
    </van-popup>
  </div>
</template>

<script>
// 这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
// 例如：import 《组件名称》 from '《组件路径》';

import { getUserProfile } from '@/api/user.js'
import UpdateName from './commponents/update-name.vue'
import UpdateGender from './commponents/update-gender.vue'
import UpdateBirthday from './commponents/update-birthday.vue'
import UpdatePhoto from './commponents/update-photo.vue'

export default {
// import引入的组件需要注入到对象中才能使用
  name: 'UserPage',
  props: {},
  components: { UpdateName, UpdateGender, UpdateBirthday, UpdatePhoto },
  data  () {
    // 这里存放数据
    return {
      user: {},
      isUpdateNameShow: false,
      isUpdateGenderShow: false, // 定义控制弹层的数据
      isUpdateBirthdayShow: false,
      isUpdatePhotoShow: false, // 控制弹层的显示
      imgPath: undefined
    }
  },
  // 监听属性 类似于data概念
  computed: {},
  // 监控data中的数据变化
  watch: {},
  // 方法集合
  methods: {
    onChangeFile (e) {
      console.log()
      // 读取文件的对象 在methods当中的this都为 vue 组件
      this.imgPath = URL.createObjectURL(e.target.files[0])
      // 读取文件完成,显示图片
      this.isUpdatePhotoShow = true
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  async created  (UpdateName) {
    const res = await getUserProfile()
    this.user = res.data.data
  },
  // 生命周期 - 挂载完成（可以访问DOM元素）
  mounted  () {

  },
  beforeCreate  () {}, // 生命周期 - 创建之前
  beforeMount  () {}, // 生命周期 - 挂载之前
  beforeUpdate  () {}, // 生命周期 - 更新之前
  updated  () {}, // 生命周期 - 更新之后
  beforeDestroy  () {}, // 生命周期 - 销毁之前
  destroyed  () {}, // 生命周期 - 销毁完成
  activated  () {} // 如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang='less' scoped>
// @import url(); 引入公共css类
.avatar {
  width: 60px;
  height: 60px;
}
.van-popup {
  background-color: #f5f7f9;
}
</style>
