<!--  -->
<template>
  <div class="">
    <van-nav-bar
      title="设置昵称"
      left-text="取消"
      right-text="完成"
      @click-left="onCancel"
      @click-right="onConfirm"
    />
    <div class="box">
      <van-field
        v-model="message"
        rows="3"
        maxlength="9"
        show-word-limit
        autosize
        type="textarea"
        placeholder="请输入昵称"
      />
    </div>
  </div>
</template>

<script>
import { updateUserProfile } from '@/api/user'
// 这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
// 例如：import 《组件名称》 from '《组件路径》';

export default {
// import引入的组件需要注入到对象中才能使用
  name: 'UpdateName',
  props: { value: String },
  components: {},
  data  () {
    // 这里存放数据
    return {
      message: this.value
    }
  },
  // 监听属性 类似于data概念
  computed: {},
  // 监控data中的数据变化
  watch: {},
  // 方法集合
  methods: {
    onCancel () {
      // 重置数据方式1 - 单个重置
      // this.message=this.value
      this.$emit('close')
    },
    async onConfirm () {
      // 通过debugger断点
      // debugger
      // 非空判断
      if (this.message.length === 0) return
      // 发送请求
      await updateUserProfile({ name: this.message })
      // 提示成功
      this.$toast.success('修改成功')
      // 修改数据
      this.$emit('input', this.message)
      // 关闭弹层
      this.$emit('close')
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created  () {

  },
  // 生命周期 - 挂载完成（可以访问DOM元素）
  mounted  () {

  },
  beforeCreate  () {}, // 生命周期 - 创建之前
  beforeMount  () {}, // 生命周期 - 挂载之前
  beforeUpdate  () {}, // 生命周期 - 更新之前
  updated  () {}, // 生命周期 - 更新之后
  beforeDestroy  () {}, // 生命周期 - 销毁之前
  destroyed  () {}, // 生命周期 - 销毁完成
  activated  () {} // 如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang='less' scoped>
// @import url(); 引入公共css类
.box {
  padding: 20px;
}
</style>
