<!--  -->
<template>
<div class='updated-photo'>
    <!-- 新建弹层内容组件 -->
    <!-- 取别名 -->
    <img :src="imgPath" ref="imgRef"/>
    <div class="footer">
        <div @click="onCancel">取消</div>
        <div @click="onConfirm">完成</div>
    </div>
</div>
</template>

<script>
// 这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
// 例如：import 《组件名称》 from '《组件路径》';
import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'
import { updateUserAvatar } from '@/api/user.js'
let cropper
export default {
// import引入的组件需要注入到对象中才能使用
  name: 'UpdatePhoto',
  props: { imgPath: [Object, String] },
  components: {},
  data  () {
    // 这里存放数据
    return {

    }
  },
  // 监听属性 类似于data概念
  computed: {},
  // 监控data中的数据变化
  watch: {},
  // 方法集合
  methods: {
    onCancel () {
      this.$emit('close') // 实现取消和确定操作
    },
    onConfirm () {
      // 获取裁剪结果
      cropper.getCroppedCanvas().toBlob(async (blob) => {
        console.log(blob)
        // 发送请求
        const formData = new FormData()
        formData.append('photo', blob)
        // 单独的接口修改头像
        const res = await updateUserAvatar(formData)
        // 提示成功
        this.$toast.success('修改成功')
        // 修改数据
        this.$emit('input', res.data.data.photo)
        // 关闭弹层
        this.$emit('close')
      })
    }
  },
  // 生命周期 - 创建完成（可以访问当前this实例）
  created  () {

  },
  // 生命周期 - 挂载完成（可以访问DOM元素）
  mounted  () {
    const image = this.$refs.imgRef
    cropper = new Cropper(image, {
      viewMode: 1, // 只能在裁剪的图片范围内移动
      dragMode: 'move', // 画布和图片都可以移动
      aspectRatio: 1, // 裁剪区默认正方形
      autoCropArea: 1, // 自动调整裁剪图片
      cropBoxMovable: false, // 禁止裁剪区移动
      cropBoxResizable: false, // 禁止裁剪区缩放
      background: false // 关闭默认背景
    })
    console.log(cropper)
  },
  beforeCreate  () {}, // 生命周期 - 创建之前
  beforeMount  () {}, // 生命周期 - 挂载之前
  beforeUpdate  () {}, // 生命周期 - 更新之前
  updated  () {}, // 生命周期 - 更新之后
  beforeDestroy  () {}, // 生命周期 - 销毁之前
  destroyed  () {}, // 生命周期 - 销毁完成
  activated  () {} // 如果页面有keep-alive缓存功能，这个函数会触发
}
</script>
<style lang='less' scoped>
// @import url(); 引入公共css类
.updated-photo{
    background-color: black;
    height:100%;
}
.footer {
      position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    font-size: 36px;
    padding: 20px;
    box-sizing: border-box;
    color: #fff;
}
</style>
